import axios from 'axios'
// import { reject, resolve } from 'core-js/fn/promise'
import { Message } from 'element-ui'

const instance = axios.create({
        time: 1000 * 60,
        /* 
        
        cookie
        
        cookie
        {withCredentials: true} */
        // withCredentials: true,
        // baseURL: 'https://netease-cloud-music-api-crete722p-hannah-bingo.vercel.app/',
        // baseURL: 'https://neteasecloudmusicapi-docs.4everland.app',
        baseURL: 'http://music.cyrilstudio.top/',
        headers: {
            'content-type': 'application/x-www-form-urlencoded'
        }
    })
    //     //cookie
    // instance.defaults.withCredentials = true
    //
instance.defaults.validateStatus = function() {
    return true
}

//
instance.interceptors.request.use(function(config) {
        //
        return config
    }, function(error) {
        //
        Message.error({ message: '' })
            // Promise rejected
        return Promise.reject(error)
    })
    // 
instance.interceptors.response.use(function(response) {
    const status = response.status
        // 
    if (status === 200) {
        //Promise
        return Promise.resolve(response)
    } else if (status === 301) {
        Message.error({ message: '' })
            // router.replace({
            //   path: 'login'
            // })
        return false
    } else {
        return Promise.reject(response)
    }
}, function(error) {
    // 
    return Promise.reject(error)
})

const ajaxMethod = ['get', 'post']
const api = {}
ajaxMethod.forEach(method => {
    //
    api[method] = function(url, data, config) {
        return new Promise(function(resolve, reject) {
            instance[method](url, data, config)
                .then(response => {
                    if (response.status === 200) {
                        resolve(response)
                    }
                })
                .catch(error => {
                    reject(error)
                })
        })
    }
})


export default api