<template>
  <div class="songs-list-main">
    <div class="list-header flex">
      <div class="columnIndex"></div>
      <div class="columnSong"></div>
      <div class="columnSinger"></div>
      <div class="columnAlbum" v-if="typeSize !== 'mini'"></div>
      <div class="columnTime"></div>
    </div>
    <div class="list-scroll" :style="{height: height + 'px'}" ref="curSongRef">
      <div class="list-main" :style="curSongSty">
        <div :class="isCurSong(item,index)" v-for="(item, index) in list" :key="item.id" @click.stop="tips($event, item)">
        <!-- <div> -->
          <!-- ++ -->
          <div class="columnIndex">
            <span class="songlist-index">{{indexMethod(index)}}</span>
            <div class="audio-icon">
              <div class="column" style="animation-delay: -1.2s;"></div>
              <div class="column"></div>
              <div class="column" style="animation-delay: -1.5s;"></div>
              <div class="column" style="animation-delay: -0.9s;"></div>
              <div class="column" style="animation-delay: -0.6s;"></div>
            </div>
            <i :class="playIcon(item)" @click="currentSong(item)"></i>
          </div>
          <!--  -->
          <div class="columnSong songlist-name">
            <router-link :class="typeSize" :to="{path: '/song',query: {id: item.id}}">
               {{item.name}}
            </router-link>
            <template>
              <router-link class="mv-name" :to="{ path: '/mv', query: { id: item.mvId }}" v-if="item.mvId">
                <i class="iconfont icon-video"></i>
              </router-link>
              <i v-if="item.vip" class="iconfont icon-vip"></i>
            </template>
          </div>
          <!--  -->
          <div class="columnSinger songlist-singer">
            <!-- uid0 -->
            <router-link :to="{path: '/singer',query: { Id: author.id }}" class="song_name" v-for="(author, k) in item.singer" :key="author.name">
               {{ k !== 0 ? '/' + author.name : author.name}}
            </router-link>
          </div>
          <!--  -->
          <div class="columnAlbum" v-if="typeSize !== 'mini'">
              <router-link class="songlist-album" :to="{ path: '/album', query: { id: item.album.id }}" v-if="item.album">{{ item.album.name }}</router-link>
          </div>
          <!--  +   -->
          <div class="columnTime">
            <div class="songlist-time">{{item.duration}}</div>
            <div class="songlist-oper">
                <i class="iconfont icon-add" title="" v-if="typeSize !== 'mini'"></i>
                <el-popover placement="bottom" trigger="click" ref="popAddList">
                    <i class="iconfont icon-add-list" title="" slot="reference" @click="closeAddListPop"></i>
                </el-popover>
                <i class="iconfont icon-collect" @click="likeSong(item)"></i>
                <i class="iconfont icon-del" title="" v-if="typeSize === 'mini'" @click.stop="delList(index)"></i>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="pagination" v-if="isShowPagination">
        <el-pagination
                background
                @current-change="currentChange"
                :page-size="this.pageSize"
                :current-page.sync="currentPage"
                layout="prev, pager, next"
                :total="this.songList.length">
            </el-pagination>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from 'vuex'
export default {
  name: 'PlayBarSongList',
  components: {

  },
  data() {
      return {
        curScroll: -1 || 1,
        // 
        currentPage: 1,
        // 
        pageSize: 30,
        playing: false,
        timer: null,
      }
  },
  props: {
    songList: {
      type: Array,
      required: true
    },
    typeSize: {   // ,normal,mini
      type: String,
      default: 'normal'
    },
    height: {
      type: [Number, String],
      default: false
    },
    // 
    stripe: {
      type: Boolean,
      default: false
    },
    //  || 
    isScroll: {
        type: Boolean,
        default: false
    },
    isShowTips: {
        type: Boolean,
        default: true 
    },
    offset: { // 
            type: Number,
            default: 0
        },
  },
  mounted() {
    this.scrollCurSong(this.curSongInfo)
  },
  computed: {
    ...mapGetters(['playList','playIndex','isPlayed']),
    curSongSty() {
      return `transform: translateY(${this.curScroll}px)`
    },
    isCurSong () {
        const self = this
        return (item, index) => {
            return ['list-item', self.stripe ? (index % 2 === 0 ? 'stripe' : '') : '', self.isPlayed && (item.id === self.curSongInfo.id) ? 'active' : '', (item.license || item.vip) ? 'disable' : '', item.vip ? 'vip' : '']
        }
    },
    list() {
      if(!this.isScroll) {  // 
        return  this.songList.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize)
      } else {
        return this.songList
      }
    },
    //   
    playIcon() {
        const self = this
        return (item) => {
            return ['iconfont','playicon', this.isPlayed && (item.id === self.curSongInfo.id) ? 'icon-pause' : 'icon-play']
        }
    },
    curSongInfo () {
        return this.playList[this.playIndex]
    },
     // 
     isShowPagination () {
        return this.songList.length > this.pageSize && !this.isScroll 
    },
  },
  methods: {
    ...mapActions(['selectPlay', 'addList']),
    ...mapMutations({
        setPlayStatus: 'SET_PLAYSTATUS',
        setPlayList: 'SET_PLAYLIST',
        setPlayListTips: 'setPlayListTips',
        setPlayIndex: 'SET_PLAYINDEX'
    }),
    // 
    indexMethod (page) {
        if (!this.isScroll) {
            return (this.currentPage - 1) * this.pageSize + page + 1 + this.offset
        } else {
            return page + 1
        }
    },
    // 
    currentSong (item) {
        //     
        if (!this.curSongInfo || item.id !== this.curSongInfo.id) {
            this.selectPlay({ list: [item] })
            if (this.isShowTips) {
                this.setPlayListTips({ flag: true, txt: '' })
                clearTimeout(this.timer)
                this.timer = setTimeout(() => {
                    this.setPlayListTips({ flag: false, txt: ' ' })
                }, 2000)
            }
        } else {
            this.setPlayStatus(!this.isPlayed)
        }
    },
    // 
    async likeSong(item) {
        const { data: res } = await this.$http.likeSong({ id: item.id, like: !item.like })
        if (res.code !== 200) {
            return this.$msg.error('')
        }
        this.$msg.success('')
    },
    //  
    delList (index) {
        this.playList.splice(index, 1) 
        this.setPlayList(this.playList)
    },
    closeAddListPop() {
        this.$refs.popAddList.forEach(item => {
            item.doClose()
        }) 
    },
    // 
    currentChange (page) {
        this.currentPage = page
    },
   
    // 
    tips (e, item) {
        if (e.target.nodeName !== 'A') {
            if (item.license) {
                this.$msg.error('')
            }

           if (item.vip) {
                this.$msg.error('')
            }
        }
    },
    // 
    scrollCurSong(cur) {
        const self = this
        if (self.isScroll) {
            // 
        }
    }
  },
   watch: {
        curSongInfo: {
            handler (val, oldVal) {
                this.scrollCurSong(val)
            },
            deep: true
        },
        songList () {
            this.currentPage = 1
        }
    },
    beforeDestroy () {
        clearTimeout(this.timer)
    }

}
</script>

<style scoped lang="less">
.list-header {
    display: flex;
    line-height: 50px;
    border-bottom: 1px solid #EBEEF5;
    font-weight: bold;
    color: #999;
}
.columnIndex {
    width: 70px;
    line-height: 50px;
    padding-left: 10px;
}
.columnSong {
    display: flex;
    flex: 1.5;
    padding-right: 10px;
    overflow: hidden;
    align-items: center;
}
.columnSinger {
    flex: 2;
    padding-right: 10px;
    overflow: hidden;
}
.columnAlbum {
    width: 200px;
}
.columnTime {
    width: 140px;
    padding-right: 10px;
    text-align: right;
}
.list-scroll {
    -moz-user-select:none; /**/
    -webkit-user-select:none; /*webkit*/
    -ms-user-select:none; /*IE10*/
    -khtml-user-select:none; /**/
    user-select:none;
    overflow-y: hidden;
}
.list-main {
    transition: transform .3s;
}
.list-item {
    display: flex;
    line-height: 0;

    .iconfont {
        margin-left: 10px;
        font-size: 22px;
        cursor: pointer;

        &:hover {
            color: @color-theme;
        }
    }

    .songlist-index {
        display: inline-block;
        width: 24px;
        text-align: center;
    }

    .playicon {
        display: none;
        margin-left: 0;
    }

    .songlist-name {
        a {
            display: inline-block;
            max-width: 60%;
            line-height: 50px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            vertical-align: top;
        }

        .mini {
            max-width: 100%;
        }

        i:hover {
            color: @color-theme;
            cursor: pointer;
        }
    }

    .songlist-singer, .songlist-album {
        display: inline-block;
        max-width: 100%;
        line-height: 50px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    .songlist-time {
        line-height: 50px;
    }

    .songlist-oper {
        display: none;
        line-height: 50px;
    }

    &.stripe {
        background: #FAFAFA;
    }

    &.active {
        background: #f0f0f0;

        .songlist-index, .playicon {
            display: none;
        }

        .audio-icon {
            display: flex;
        }

        &:hover {
            .playicon {
                display: block;
                color: @color-theme;
            }
            .audio-icon {
                display: none;
            }
        }
    }

    &:hover {
        background: #f0f0f0;

        .songlist-time, .songlist-index {
            display: none;
        }

        .songlist-oper, .playicon {
            display: block;
        }
    }

    .icon-vip {
        font-size: 30px;
        color: @color-theme;
    }

    &.disable {
        color: #ccc;

        a {
            color: #ccc;
        }

        .playicon, .songlist-oper {
            display: none;
        }

        .songlist-index, .songlist-time {
            display: block;
        }
    }

    &.vip {
        color: @color-theme;

        a {
            color: @color-theme;
        }
    }
}

.song-list {

    .songlist-singer {
        display: inline-block;
        width: 100px;
        height: 23px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    .songlist-name {
        width: 210px;

        a {
            display: inline-block;
            max-width: 185px;
            height: 23px;
            margin-right: 5px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }

        i:hover {
            color: @color-theme;
            cursor: pointer;
        }
    }

    .songlist-album {
        display: inline-block;
        width: 95%;
        height: 23px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    .songlist-oper {
        display: none;

        .iconfont {
            margin-left: 15px;
            font-size: 22px;
            cursor: pointer;

            &:hover {
                color: @color-theme;
            }
        }
    }
}
.pagination {
    padding: 50px 0 0;
    text-align: center;
}
</style>
